.container{
    display: flex;
    height: 100vh;
    width: 100%;
   justify-content: center;
   align-items: center;
   position: relative;
   img{
    width: 100px;
    height: 100px;
    position: absolute;
    left: 46%;
    top: 15%;
   }

   .reset-btn{
    top: 75%;
    left: 43%;
    position: absolute;

   }

   .hold-btn{
    top: 70%;
    left: 50%;
   }

   .roll-dice{
    margin-top: 20px;
   }

   .player-board-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: bisque;
    padding-bottom: 2rem;
   }
   .check-winner-btn{
    position: absolute;
    top: 45%;
    left: 43%;
   }

   
}


.game-rules{
    margin-left: 110px;
    font-size: 2rem;
}